@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  font-family: var(--font-geist-sans), Arial, Helvetica, sans-serif;
}

@layer base {
  :root {
    --background: 0 0% 100%; /* White */
    --foreground: 240 10% 3.9%; /* Dark Gray for text */
    --card: 0 0% 100%; /* White */
    --card-foreground: 240 10% 3.9%;
    --popover: 0 0% 100%;
    --popover-foreground: 240 10% 3.9%;
    --primary: 207 70% 53%; /* Soft Blue */
    --primary-foreground: 0 0% 100%; /* White */
    --secondary: 0 0% 94.1%; /* Light Gray */
    --secondary-foreground: 240 10% 3.9%;
    --muted: 0 0% 96.1%;
    --muted-foreground: 0 0% 45.1%;
    --accent: 207 70% 53%; /* Soft Blue */
    --accent-foreground: 0 0% 100%; /* White */
    --destructive: 0 84.2% 60.2%; /* Red for destructive actions / pending tasks */
    --destructive-foreground: 0 0% 98%;
    --status-pending: 0 84.2% 60.2%; /* Red for pending tasks */
    --status-completed: 145 63% 49%; /* Green for completed tasks */
    --border: 0 0% 89.8%; /* Slightly darker gray for borders */
    --input: 0 0% 89.8%;
    --ring: 207 70% 53%; /* Soft Blue for focus rings */
    --chart-1: 12 76% 61%;
    --chart-2: 173 58% 39%;
    --chart-3: 197 37% 24%;
    --chart-4: 43 74% 66%;
    --chart-5: 27 87% 67%;
    --radius: 0.5rem;
    --sidebar-background: 0 0% 98%;
    --sidebar-foreground: 240 5.3% 26.1%;
    --sidebar-primary: 207 70% 53%;
    --sidebar-primary-foreground: 0 0% 100%;
    --sidebar-accent: 0 0% 94.1%;
    --sidebar-accent-foreground: 240 10% 3.9%;
    --sidebar-border: 220 13% 91%;
    --sidebar-ring: 207 70% 53%;

    /* Task List Item Colors */
    --list-item-color-1: var(--chart-1);
    --list-item-color-2: var(--chart-2);
    --list-item-color-3: var(--chart-3);
    --list-item-color-4: var(--chart-4);
    --list-item-color-5: var(--chart-5);
    --list-item-color-default: var(--muted);
  }
  .dark {
    --background: 240 10% 3.9%;
    --foreground: 0 0% 98%;
    --card: 240 10% 3.9%;
    --card-foreground: 0 0% 98%;
    --popover: 240 10% 3.9%;
    --popover-foreground: 0 0% 98%;
    --primary: 207 70% 53%; /* Soft Blue */
    --primary-foreground: 0 0% 100%; /* White */
    --secondary: 240 3.7% 15.9%;
    --secondary-foreground: 0 0% 98%;
    --muted: 240 3.7% 15.9%;
    --muted-foreground: 0 0% 63.9%;
    --accent: 207 70% 53%; /* Soft Blue */
    --accent-foreground: 0 0% 100%; /* White */
    --destructive: 0 72.2% 50.6%;
    --destructive-foreground: 0 0% 98%;
    --status-pending: 0 72.2% 50.6%;
    --status-completed: 145 58% 42%;
    --border: 240 3.7% 15.9%;
    --input: 240 3.7% 15.9%;
    --ring: 207 70% 53%; /* Soft Blue */
    --chart-1: 220 70% 50%;
    --chart-2: 160 60% 45%;
    --chart-3: 30 80% 55%;
    --chart-4: 280 65% 60%;
    --chart-5: 340 75% 55%;
    --sidebar-background: 240 5.9% 10%;
    --sidebar-foreground: 0 0% 95.9%;
    --sidebar-primary: 207 70% 53%;
    --sidebar-primary-foreground: 0 0% 100%;
    --sidebar-accent: 240 3.7% 15.9%;
    --sidebar-accent-foreground: 0 0% 95.9%;
    --sidebar-border: 240 3.7% 15.9%;
    --sidebar-ring: 207 70% 53%;

    /* Task List Item Colors */
    --list-item-color-1: var(--chart-1);
    --list-item-color-2: var(--chart-2);
    --list-item-color-3: var(--chart-3);
    --list-item-color-4: var(--chart-4);
    --list-item-color-5: var(--chart-5);
    --list-item-color-default: var(--muted);
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
  }
}